import React from "react";
import styled from "styled-components";
import logo from "../img/logo.jpg";

const P = styled.p`
  text-align: ${props => (props.align ? "left" : "center")};
  text-transform: uppercase;
  font-size: ${props => (props.size ? "36pt" : "120%")};
  color: ${props => (props.color ? "black" : "white")};
`;
const P1 = styled(P)`
  grid-area: 2 / 1 / 3 / 3;
  place-self: start center;
  margin: 0px;
`;
const P2 = styled(P)`
  grid-area: auto / auto / 4 / 2;
  place-self: center center;
`;
const H1 = styled.h1`
  grid-area: 1 / 1 / 2 / 2;
  text-transform: uppercase;
  font-size: 300%;
  color: white;
  margin: 0px;
  margin-top: 13px;
`;
const Img = styled.img`
  grid-area: 1 / 2 / 2 / 3;
  justify-self: end;
  width: 80px;
  height: 80px;
  @media (max-width: 720px) {
    display: none;
  }
`;
const Button = styled.button`
  grid-area: 3 / 2 / 4 / 3;
  place-self: center center;
  width: 200px;
  height: 50px;
  background: yellow;
  border-radius: 30px;
  font-size: 120%;
  text-transform: uppercase;
`;
const Course = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(303.5px, 1fr));
  grid-template-rows: 1fr 1fr 1fr;
  background: #833ae1;
  height: 300px;
`;

export default () => (
  <Course>
    <H1>Креативное программирование</H1>
    <Img src={logo} alt="Алгоритмика" />
    <P1 align>Приглашаем вас посетить бесплатный мастер-класс в Реутове</P1>
    <P2 align>Курс для детей 8-12 лет</P2>
    <Button>Записаться</Button>
  </Course>
);
