import React from "react";
import styled from "styled-components";

const Header = styled.header`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
`;
const Active = styled.a`
  grid-area: 1 / 1 / 2 / 2;
  background: #833ae1;
  border-top-right-radius: 20px;
  color: ${props => (props.color ? "white" : "black")};
  text-transform: uppercase;
  text-align: center;
  font-size: 120%;
`;
const A2 = styled(Active)`
  grid-area: 1 / 2 / 2 / 3;
  background: yellow;
`;
const A3 = styled(Active)`
  grid-area: 1 / 3 / 2 / 4;
  background: yellow;
`;

export default () => (
  <Header>
    <Active color>8-12 Лет</Active>
    <A2>7-12 Лет</A2>
    <A3>11-13 Лет</A3>
  </Header>
);
