import React from "react";
import styled from "styled-components";

const Footer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #833ae1;
  height: 100px;
`;
const P = styled.p`
  place-self: center center;
  text-transform: uppercase;
  font-size: 120%;
  color: white;
  text-align: center;
`;
const Button = styled.button`
  place-self: center center;
  width: 200px;
  height: 50px;
  background: yellow;
  border-radius: 30px;
  font-size: 120%;
  text-transform: uppercase;
`;

export default () => (
  <Footer>
    <P>Записаться на бесплатный мастер класс</P>
    <Button>Записаться</Button>
  </Footer>
);
