import React from "react";
import styled from "styled-components";

const Table = styled.div`
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-template-rows: 45px 45px 30px 45px 45px 70px 25px;
  grid-gap: 1em 1em;
`;
const H2 = styled.h2`
  grid-area: 1 / 1 / 2 / 3;
`;
const H22 = styled.h2`
  grid-area: 8 / 1 / 9 / 3;
`;
const P = styled.div`
  grid-area: 2 / 1 / 3 / 3;
`;

const P1 = styled.div`
  grid-area: 3 / 1 / 4 / 2;
`;

const P12 = styled.div`
  grid-area: 3 / 2 / 4 / 3;
`;

const P2 = styled.div`
  grid-area: 4 / 1 / 5 / 2;
`;

const P22 = styled.div`
  grid-area: 4 / 2 / 5 / 3;
`;

const P3 = styled.div`
  grid-area: 5 / 1 / 6 / 2;
`;

const P32 = styled.div`
  grid-area: 5 / 2 / 6 / 3;
`;

const P4 = styled.div`
  grid-area: 6 / 1 / 7 / 2;
`;

const P42 = styled.div`
  grid-area: 6 / 2 / 7 / 3;
`;

const P5 = styled.div`
  grid-area: 7 / 1 / 8 / 2;
`;

const P52 = styled.div`
  grid-area: 7 / 2 / 8 / 3;
`;

export default () => (
  <Table>
    <H2>Как проходит мастер класс</H2>
    <P>
      Продолжительность - 45 минут <br />
      Ход мастер-класса:
    </P>
    <P1>Знакомство</P1>
    <P12>
      Знакомим учеников с сюжетом урока и детей друг с другом и с
      преподавателями
    </P12>
    <P2>«Учительробот»</P2>
    <P22>
      Вовлекаем детей в эксперимент с программированием робота, чтобы они вывели
      для себя понятие “алгоритм”
    </P22>
    <P3>Обсуждение правил</P3>
    <P32>
      Обсуждаем основные правила поведения на занятиях/ договариваемся о них
      совместно
    </P32>
    <P4>Программирование</P4>
    <P42>
      Знакомим детей с платформой и сюжетом (Покорение Марса). в процессе
      прохождения уровней отрабатываем навык составления линейных алгоритмов для
      достижения цели
    </P42>
    <P5>Рефлексия</P5>
    <P52>
      Обсуждаем практическую ценность программирования в связке с реальной
      жизнью
    </P52>
    <H22>Посмотрите живой отзыв от нашей ученицы</H22>
  </Table>
);
